<template>

<div id="carcontent">
	<div class="branch" v-if="0"><img :src="list.branch" /></div>

	<div v-for="item in list" class="sote">
		<h3 class="fa fa-bookmark" v-if='item.name'><router-link :to="{ path: '/catelist/'+ item.id }">{{item.name}}</router-link></h3>
		<ul v-if="item.has_many_children">
			<li v-for="catshop in item.has_many_children">
				<router-link :to="{ path: '/catelist/'+ catshop.id }">
					<div class="thumb" v-if="catshop.thumb"><img :src="catshop.thumb" /></div>
					<div class="thumb" v-if="!catshop.thumb"><img src="../assets/images/img_default.png" /></div>
					<span>{{catshop.name}}</span>
				</router-link>
			</li>



		</ul>
	</div>
</div>

</template>
<script>

	export default {
		props: ['list'],
    data() {
      return {
      	allLoaded:false,
        topStatus: ''
        // ...
      };
    },
    methods: {

    }
	}

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
#carcontent{background: #FFF;color: #686868;margin-bottom: 110px;
	.sote{clear: both;overflow: hidden;}
	h3{text-align: left;font-size: .9rem;margin: 0;    padding: 10px 7px;
		font-weight: normal;
		display: block;
		clear: both;
		color: #999;

		a{color: #666;}
	}
	.fa-bookmark:before{margin-right: 6px;}
	a span{   height: 30px;
		line-height: 30px;
    overflow: hidden;color: #686868;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    word-break: break-all;
    overflow: hidden;}
	.branch img{width: 100%;}
	.thumb{line-height: 22.5vw;height: 22.5vw;overflow: hidden; box-sizing: border-box;}
	ul{    clear: both;
		width: 100%;
		overflow: hidden;padding: 0 0 0 3%;}
	li{width: 29%;text-align: center; float: left;margin-bottom: 10px;margin-right: 3%;font-size: .7rem; background:#fff;
		img{width: 100%;}
	}
}
</style>


