	//import { mapState } from 'vuex';
	import cTitle from 'components/title';
	import cRootlist from 'components/rootlist';
	import cCarcontent from 'components/carcontent';
	import { Loadmore } from 'mint-ui';
	import BScroll from 'better-scroll';
	import { mapState,mapMutations } from 'vuex';
	
	export default {
		data() {
			return {
				active: 0,
				allLoaded: false,
				topStatus: '',
				err:'',
				turl: '/category',
				arg: { index: 10, item: 'pinch' },
				list: [],
				contentlist: {}
			}
		},
		computed: mapState(["category",'blurst']),
		methods: {
			tabsa(n, i) {
				this.active = n;
				this.getContentlist(i);
			},
			bpfun(){
				this.$router.push('/brand');
			},
			getList()
			{
				 $http.get('goods.category.get-category',{}).then((response)=>
				{
						if(response.result == 1){
							var myData = response.data;
							this.list = response.data.data;
						}		
				}, function (response) {
											// error callback
										});			
			},
			getContentlist(id){
				
				$http.get('goods.category.get-children-category',{parent_id:id}).then((json) => {
					if(json.result == 1) {
						this.err=false;
						this.contentlist=json.data;
						 this.$nextTick(() => {
						 	this._initScroll(this.$refs.menucontent)
						 })
					} else {
						this.err=true;
						this.doException(json);
					}
				});					
			},
			_initScroll (doc) {
				this.menuScroll = new BScroll(doc, {click: true})  
			}
		},
		mounted(){
			this.getList();
			this.getContentlist(3);

		},
		components: { cTitle, cCarcontent }
	}


